<template>
  <div
    id="UserForm"
    :style="
      theme
        ? 'background: #0d1117;'
        : 'background: #faf8ff;'
    "
  >
    <HeaderInfo />

    <!--  -->
    <el-divider content-position="left"></el-divider>
    <div class="userForm">
      <el-form class="info" :model="UserForm" size="small">
        <!--  -->
        <el-form-item label="">
          <br>
          <p>{{ UserForm.userName }}</p>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input v-model="UserForm.name"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select v-model="UserForm.Gender" placeholder="">
            <el-option
              v-for="(Gender, index) in Genders"
              :key="index"
              :label="Gender"
              :value="index"
            />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select v-model="UserForm.Star" placeholder="">
             <el-option
              v-for="(Star, index) in Stars"
              :key="index"
              :label="Star"
              :value="index"
            />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select v-model="UserForm.feeling" placeholder="">
             <el-option
              v-for="(feeling, index) in feelings"
              :key="index"
              :label="feeling"
              :value="index"
            />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-select v-model="UserForm.blood" placeholder="">
             <el-option
              v-for="(blood, index) in bloods"
              :key="index"
              :label="blood"
              :value="index"
            />
          </el-select>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input v-model="UserForm.mail"></el-input>
        </el-form-item>

        <!-- QQ -->
        <el-form-item label="QQ">
          <el-input v-model="UserForm.QQ"></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input type="textarea" v-model="UserForm.autograph"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" size="small"></el-button>
        </el-form-item>
      </el-form>

      <!--  -->
      <div class="userPhoto">
        <img
          class="userImg"
          :src="UserForm.userPhoto"
          @click="$refs['file-inp'].click()"
        >
        <!--  -->
        <div
          class="changePhoto"
          @click="$refs['file-inp'].click()"
        >
          <i class="tyh tyh-pan_icon" />
          
        </div>
        <input
          type="file"
          ref="file-inp"
          accept="image/*"
          hidden
          @change="changePhoto"
        />

        <!--  -->
        <el-dialog
          title=""
          :visible.sync="photoDialog"
          @opened="onOpenedPhoto"
          @closed="onClosedRemove"
        >
          <div>
            <img
              style="width: 100%; max-height: 270px"
              :src="photoBlob"
              ref="user-photo"
            />
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button @click="photoDialog = false"> </el-button>
            <el-button type="primary" @click="photoDialog = false"> </el-button>
          </span>
        </el-dialog>
      </div>
    </div>

    <el-divider content-position="left"></el-divider>
    <div class="userForm">
      <el-form class="info" size="small">
        <!--  -->
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <el-input></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" size="small"></el-button>
        </el-form-item>
      </el-form>
      <div class="userPhoto" />
    </div>

    <el-divider content-position="left"></el-divider>
    <div class="userForm">
      <div class="info">
        <el-button
          type="primary"
          size="small"
          class="outLogin"
          @click="outLogin"
        >
          
        </el-button>
      </div>
      <div class="userPhoto" />
    </div>
  </div>
</template>

<script>
import { Message } from 'at-ui'
// 
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
//  - 
import { getSettingSelect, onLogin } from '@/api/user'
//  vuex
import { mapState } from 'vuex'
import HeaderInfo from './HeaderInfo'
export default {
  name: 'UserForm',
  components: {
    HeaderInfo
  },
  data () {
    return {
      UserForm: {},
      Genders: [], // 
      Stars: [], // 
      feelings: [], // 
      bloods: [], // 
      photoDialog: false, // 
      photoBlob: null // 
    }
  },
  computed: {
    ...mapState(['userInfo', 'theme'])
  },
  created () {
    this.loadgetSettingSelect() // 
    this.loadonLogin() // 
  },
  methods: {
    // 
    changePhoto () {
      const file = this.$refs['file-inp']
      const blob = window.URL.createObjectURL(file.files[0])
      this.photoBlob = blob
      this.photoDialog = true
    },
    //  
    onOpenedPhoto () {
      const image = this.$refs['user-photo']
      this.Cropper = new Cropper(image, {
        aspectRatio: 1 / 1,
        viewMode: 1, // 
        dragMode: 'none' // 
      })
    },
    //  
    onClosedRemove () {
      this.$refs['file-inp'].value = ''
      this.Cropper.destroy() // 
    },
    // 
    async loadgetSettingSelect () {
      const { data } = await getSettingSelect()
      this.Genders = data.Gender
      this.Stars = data.Star
      this.bloods = data.blood
      this.feelings = data.feeling
    },
    // 
    async loadonLogin () {
      const { data } = await onLogin()
      for (const key in data) {
        const userName = data[key].userInfo.userName
        if (this.userInfo.userName === userName) {
          this.UserForm = data[key].userInfo
        }
      }
    },
    // 
    outLogin () {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        window.localStorage.removeItem('userInfo')
        this.$store.commit('changeUserInfo', null)
        Message.success({
          message: '',
          duration: 900
        })
        this.$router.push('/')
      }).catch(() => {})
    }
  }
}
</script>

